<template>
    <div id="login" >
        <div class="container">
            <div class="row justify-content-center">
                <div class="col-sm-5 col-12">
                    <div class="card">
                        <div class="card-header">Login</div>

                        <div class="card-body">
                            <form @submit.prevent="submit">
                                <div class="form-group row">
                                    <label for="email" class="col-md-4 col-form-label text-md-right">E-mailadres</label>

                                    <div class="col-md-6">
                                        <input id="email" v-model="form.email" type="email" class="form-control" name="email" autofocus />
                                    </div>
                                </div>

                                <div class="form-group row">
                                    <label for="password" class="col-md-4 col-form-label text-md-right">Wachtwoord</label>

                                    <div class="col-md-6">
                                        <input id="password" v-model="form.password" type="password" class="form-control" name="password" required autocomplete="current-password">

                                    </div>
                                </div>

                                <div class="form-group row">
                                    <div class="col-md-6 offset-md-4">
                                        <div class="form-check">
                                            <input class="form-check-input" v-model="form.remember" type="checkbox" name="remember" id="remember">

                                            <label class="form-check-label" for="remember">
                                                Onthouden
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group row mb-0">
                                    <div class="col-md-8 offset-md-4">
                                        <button type="submit" class="btn btn-primary">
                                            Login
                                        </button>


                                        <a class="btn btn-link" href="">
                                            Wachtwoord vergeten?
                                        </a>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        metaInfo: { title: 'Login' },
        props: {
            errors: Object,
        },
        data() {
            return {
                sending: false,
                form: {
                    email: null,
                    password: null,
                    remember: null,
                },
            }
        },
        methods: {
            submit() {
                this.sending = true
                this.$inertia.post(this.route('login.attempt'), {
                    email: this.form.email,
                    password: this.form.password,
                    remember: this.form.remember,
                }).then(() => this.sending = false)
            },
        },
    }
</script>